}
/* Custom Select */
.select {
  position: relative;
  display: -webkit-flex;
  display: flex;
  width: 20em;
  margin: 0 1rem;
  height: 2.1em;
  line-height: 3;
  background: transparent;
  overflow: hidden;
  border: 1px solid white;
  border-radius: 3px;
}
select {
  -webkit-flex: 1 1;
          flex: 1 1;
  padding: 0 0.5em;
  color: #fff;
  cursor: pointer;
}
/* Arrow */
.select::after {
  content: "\25BC";
  color: white;
  padding: 0 1em;
  background: rgba(255, 255, 255, 0.425);
  cursor: pointer;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  pointer-events: none;
  transition: 0.25s all ease;
}
/* Transition */
.select:hover::after {
  color: #2f67cd;
  background: white;
}

